.darkmode-toggler {
  margin: 0;
  padding: 0;
  vertical-align: middle;
}
.darkmode-toggler::before {
  cursor: pointer;
  content: var(--emoji, "ðŸ˜€");
  display: block;
  font-size: 2rem;
  line-height: 1;
}

.btn {
  transition: box-shadow 0.25s ease;
  cursor: pointer;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: var(--border-radius);
}
.btn:hover {
  background-color: var(--bg-clr-light);
}
.btn:active {
  background-color: var(--bg-clr-dark);
}
.btn:focus {
  box-shadow: 0 0 0 5px var(--bg-clr-half);
}

.nav-list {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin: 0;
  list-style: none;
}
.nav-list.nav-tabs, .nav-list.nav-tabs .nav-item {
  background-color: var(--bg-clr-dark);
  border-radius: var(--border-radius);
}
.nav-link {
  display: inline-block;
  padding: 0.5rem;
  color: var(--text-clr-darker);
  text-decoration: none;
  border-radius: var(--border-radius);
}
.nav-link.active {
  color: var(--text-clr-lighter);
  background-color: var(--bg-clr-dark);
}
.nav-tabs .nav-link.active {
  background-color: var(--bg-clr-darker);
}

.nav-link:hover {
  background-color: var(--bg-clr-darker);
}
.nav-link:hover[aria-disabled=true] {
  background-color: var(--bg-clr-dark);
}
.nav-tabs .nav-link {
  padding: 0.5rem 1rem;
}

.navbar {
  border-bottom: 1px solid var(--secondary);
}
.navbar-logo {
  margin-right: 1.5rem;
  padding: 1rem 0;
  font-size: 1.25rem;
  background-color: inherit !important;
}
.navbar-toggler {
  transition: box-shadow 0.25s ease;
  cursor: pointer;
  display: none;
  padding: 0.25rem 0.75rem;
  appearance: none;
  background: transparent;
  border: 1px solid var(--secondary);
  border-radius: var(--border-radius);
}
.navbar-toggler:focus {
  box-shadow: 0 0 0 5px var(--secondary-half);
}
.navbar-toggler::before {
  content: "";
  display: block;
  width: 30px;
  height: 30px;
  background-color: var(--secondary);
  mask-image: url("data:image/svg+xml,%3Csvg%20xmlns='http://www.w3.org/2000/svg'%20viewBox='0%200%2030%2030'%3E%3Cpath%20stroke='black'%20stroke-linecap='round'%20stroke-miterlimit='10'%20stroke-width='2'%20d='M4%207h22M4%2015h22M4%2023h22'/%3E%3C/svg%3E");
}
@media (width <= 768px) {
  .navbar-toggler {
    display: block;
  }
}
.navbar-collapse {
  display: flex;
  flex-basis: auto;
  flex-grow: 1;
  align-items: center;
}
@media (width <= 768px) {
  .navbar-collapse {
    transition-timing-function: ease;
    transition-duration: 350ms;
    overflow: hidden;
    display: block;
    flex-basis: 100%;
    max-height: 0;
    transition-property: max-height;
  }
  .navbar-collapse::before {
    content: "";
    display: block;
    width: 100%;
    height: 2px;
    background-color: var(--secondary);
  }
}
.navbar .container {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  justify-content: space-between;
}
@media (width <= 768px) {
  .navbar .container {
    align-items: center;
  }
}
@media (width <= 768px) {
  .navbar .nav-list {
    flex-direction: column;
    gap: 1rem;
    padding: 1rem 0;
    background-color: inherit !important;
  }
}
.navbar .nav-link {
  display: block;
}
@media (width <= 768px) {
  .navbar-toggler:checked + .navbar-collapse {
    max-height: var(--height, max-content);
  }
}

/* index.scss */
html,
body {
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

* {
  box-sizing: border-box;
  padding: 0;
}
*[aria-disabled=true] {
  cursor: not-allowed;
  color: var(--text-clr-half);
  text-decoration: line-through;
}
*[aria-disabled=true]:active {
  pointer-events: none;
}

